<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-lg-6 col-md-8 col-10">
        <div class="jumbotron">
          <hr>
          <p v-if="error.length===0">Create new post</p>
          <p v-else class="errorMessage" style="font-size: 16px">
            <i class="fas fa-exclamation-triangle fa-lg"></i>
            <span> {{ error }} </span>
          </p>
          <hr>
        </div>

        <form @submit.prevent="createPost()">
          <!-- Caption -->
          <template>
            <div class="input-group form-group">
              <label class="sr-only" for="formCaption">Caption</label>
              <div class="input-group-prepend">
                <div class="input-group-text"> <i class="fas fa-keyboard fa-fw fa-lg"></i> </div>
              </div>

              <input 
                @blur="$v.info.caption.$touch()"
                v-model="info.caption"
                class="form-control"
                :class="{'is-invalid': $v.info.caption.$error}"
                id="formCaption"
                placeholder="Caption"
                name="caption" 
                type="textarea"
              >
            </div>
            <!-- <template v-if="$v.info.caption.$error">
                <p v-if="!$v.info.caption.$required" class="errorMessage text-left">
                    <i class="fas fa-exclamation-triangle fa-lg"></i>
                    <span> Caption missing </span>
                </p>
            </template> -->
          </template>

          <!-- Image input -->
          <template>
            <div class="input-group form-group" id="pass">
              <label class="sr-only" for="formImageInput">Image Input</label>
              <div class="input-group-prepend">
                  <div class="input-group-text"> <i class="fas fa-image fa-fw fa-lg"></i> </div>
              </div>
              <input 
                @blur="$v.info.imageInput.$touch()"
                @change="readFiles"
                class="form-control"
                :class="{'is-invalid': $v.info.imageInput.$error}"
                id="formimageInput" 
                placeholder="Image Input" 
                name="imageInput"
                type="file"
              >
            </div>
            <!-- <template v-if="$v.info.imageInput.$error">
              <p v-if="!$v.info.imageInput.$required" class="errorMessage text-left">
                <i class="fas fa-exclamation-triangle fa-lg"></i>
                <span> Image required </span>
              </p>
            </template> -->
          </template>
          
          
          <!-- Upload in button -->
          <template>
            <button 
              @click="$v.$touch()"
              :disabled="$v.$anyError"
              type="submit"
              class="btn btn-success btn-lg btn-block"
            > 
              Upload
            </button>
          </template>

        </form>
      </div>
    </div>

  </div>
</template>

<script>
  import utils from '@/utils.js';
  import NProgress from 'nprogress';
  import EventService from '@/services/EventService.js';
  import { required } from 'vuelidate/lib/validators';

  export default {
    name: 'CreateNewPost',
    data(){
      return {
        isLoggedIn: false,
        info: {
          caption: '',
          imageInput: '',
        },
        error: '',
      }
    },    

    methods: {
      async createPost (){
        NProgress.start();

        await this.checkLoginStatus();

        if(this.isLoggedIn) {
          let formData = new FormData();
          formData.append('image', this.imageInput);
          formData.append('caption', this.caption);
          /* EventService.createNewPost(formData)
            .then((res)=>{
              if (res.data[1].catagory.toLowerCase() === 'success'){
                console.log(res.data[0])
                this.$store.dispatch('postStore/addNewPost', res.data[0]);
                this.$router.push({name: 'Newsfeed'})
              } else {
                this.$store.dispatch('notificationStore/showNotification', res.data[1])
              }
              NProgress.done();
            })
            .catch((err)=>{
              this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Connection Problem', message: err.message});
              NProgress.done();
            })
          } else {
            this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Access denied', message: 'You need to be logged in for this operartion'});
            NProgress.done(); */
          }
      },
      
      readFiles (element){
        var file = element.target.files[0];
        this.info.imageInput = file;    
      },


      checkLoginStatus (){
        return EventService.isLoggedIn()
          .then (res => {
            this.isLoggedIn = res.data[1];
            this.$store.dispatch('postStore/saveUser', res.data[0]);
          })
          .catch (err => {throw err})
      },
    },

    computed: {
      /* hi: function (){ return reader.result} */
    },

    validations: {
      info: {
        caption: { required },
        imageInput: {
          required
          }
        
      }
    },

    components: {
      
    }
      
  }
</script>

<style lang="css" scoped>
  /* --------------------( body )-------------------- */
  body {
    margin: 1px auto 60px auto;
    font-family: ubuntu;
  }

  /* --------------------( ^ body )-------------------- */

  .jumbotron {
    margin-top: 2%;
    margin-bottom: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    font-family: ubuntu;
  }

  form {
    margin: 15% auto;
  }

  #pass {
    margin-top: 6%;
  }

  form button {
    margin-top: 10%;
    font-family: 'Andika New Basic';
    width: 200px;
  }

  #goRegister {
    text-align: right;
    margin-top: 5%;
    margin-right: 10px;
  }

  #goRegister a {
    font-family: 'amaranth';
  }

  .error-box {
    box-shadow: 5px 5px 10px red;
  }

  .errorMessage {
    margin-left: 20px;
    font-family: 'Kite One';
    font-size: 14px;
  }

  .errorMessage i {
    color: red;
  }

  .errorMessage span {
    margin-left: 5px;
    color: red;
    letter-spacing: 1px;
    font-weight: bolder;
    text-shadow: 5px 5px 8px #676565;
  }
</style>